import { Input, Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, AbstractControl} from '@angular/forms';
import {Observable} from 'rxjs';

import {User} from '../../modelos/user.model';
import {Session} from '../../modelos/session.model';
import {Country} from '../../modelos/country.model';
import { RegisterResponse } from './../../modelos/Response/RegisterResponse.model';

import { CacheStorageService } from './../../services/cachestorage.service';
import {UserService} from './../../services/user.service';
import {StorageServiceService} from '../../services/storage-service.service';

@Component({
  selector: 'app-profile',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.css']
})

export class EditComponent implements OnInit {
  user: User = new User();
  formularioProfile: FormGroup;
  formularioPassword: FormGroup;
  confirmpassoword: string;
  static IsEquasError:boolean = false;
  mensajeProfile: any = [];
  mensajePass: any = [];
  countryName : string;
  country : Country[] = [];
  results$:Observable<Session>;
  results2$:Observable<RegisterResponse>;

  constructor(private storage: StorageServiceService,  public fb: FormBuilder, public fb2: FormBuilder,
    private cacheManager: CacheStorageService, private serviceuser: UserService) 
  { 
    this.country.push(new Country());
    this.getCountrys();
  }

  ngOnInit() {
    this.user = this.storage.getCurrentUser();
    console.log(this.user);
    
    this.formularioProfile = this.fb.group({
      email: ['', Validators.compose([Validators.required, Validators.email])],
      cell: ['', [Validators.required]],
      SelectCountry: ['', [Validators.required]]
    });

    this.formularioPassword = this.fb2.group(
    {
      password: ['', Validators.compose([Validators.required, Validators.minLength(7)])],
      confirmPassword: ['', [Validators.required]]
    },
    {
      validator: [EditComponent.MatchPassword, EditComponent.EvaluateStrength]
    }
    );
  }

  get f()
  {
    let respuesta = this.formularioProfile;
    return respuesta.controls;
  }

  get fP()
  {
    let respuesta = this.formularioPassword;
    return respuesta.controls;
  }

  private getCountrys() {
    this.cacheManager.getData<Country>("Country", 
    (array : Country[]) =>
    {
        this.country = array;
        this.ActualizarNombrePais();
    });
  }

  ActualizarNombrePais()
  {
    this.country.forEach(element => { 
      if(element.IdCountry == this.user.IdCountry)
        this.countryName = element.Name;
    });
  }

  registerUser() 
  {
    debugger;
  	if (this.formularioProfile.invalid || !this.user.IdCountry || this.user.IdCountry == ''){
      return;
    }    

    this.results$ = this.serviceuser.updateUser(this.user);
    this.results$.subscribe(respuesta=>
    {
      var sesResponse: Session = respuesta;
      if(sesResponse && sesResponse.JSonResult && sesResponse != null && sesResponse.IsProcesed && sesResponse.JSonResult != null)
      {
          this.mensajeProfile = [{ id:2, type:'success', message: "Su perfil ha sido actualizado!" }];
          var infoSession: Session = this.storage.getCurrentSession()
          
          this.storage.setCurrentSession(sesResponse);
          this.ActualizarNombrePais();
      }
      else{
          this.mensajeProfile = [{ id:2, type:'warning', message: sesResponse.Message }];
      }
    },error=>{
        this.mensajeProfile = [{ id:2, type:'danger', message:error.tatusText }];
    });
  }

  changePassword() {
  	if (this.formularioPassword.invalid){
      return;
    }    
   
    this.results2$ = this.serviceuser.changePassword(this.user);
    this.results2$.subscribe(respuesta=>
    {
      var userResponse: RegisterResponse = respuesta;
      if(userResponse != null && userResponse.Exitoso)
      {
          this.mensajePass = [{ id:2, type:'success', message: "Su contraseña ha sido cambiada!" }];
      }
      else{
          this.mensajePass = [{ id:2, type:'warning', message: userResponse.MensajeResultado }];
      }
    },error=>{
        this.mensajePass = [{ id:2, type:'danger', message:error.tatusText }];
    });
  }

  static MatchPassword(AC: AbstractControl) 
  {
      let password = AC.get('password').value;
      let confirmPassword = AC.get('confirmPassword').value;
      if(password != confirmPassword) {
        EditComponent.IsEquasError = true;
          AC.get('confirmPassword').setErrors( {MatchPassword: true} )
      }
      else {
          return null
      }
  }

  static EvaluateStrength(AC: AbstractControl) 
  {
      let password = AC.get('password').value;
      let nivel = EditComponent.pwstrength(password);
      if(nivel < 3) 
      {
          EditComponent.IsEquasError = true;
          AC.get('password').setErrors( {EvaluateStrength: true} )
      }
      else {
          return null
      }
  }

  static pwstrength(n: string) 
  {
    if(n == null || n == "")
      return 0;

    var t = 0, u = n.length, f, i, r;
    return (t += u < 5 ? 0 : u < 8 ? 5 : u < 16 ? 10 : 15, i = n.match(/[a-z]/g), i && (t += 1), r = n.match(/\d/g), r && r.length > 1 && i && (t += 15), f = n.match(/[A-Z]/g), f && (t += 5), f && i && r && (t += 5), n.match(/\s/) && (t += 10), t < 15) ? 0 : t < 20 ? 1 : t < 35 ? 2 : 3
  }
}


export interface IAlert {
  id: number;
  type: string;
  message: string;
}